.activity-description {
  text-align: center;
  background-image: url('../imges/background.jpg');
  background-size: cover;
  height: 100vh;
  padding-top: 9rem;
}

.event-name {
  font-size: 2rem;
  color: #FFFFFF;
  background-image: url('../imges/logo.jpg');
  background-size: cover;
  width: 12.5rem;
  height: 6rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.event-directions{
  font-size: 1.5rem;
  margin: 4rem auto;
}

.shop-list {
  margin: 4rem auto;
  font-size: 1.5rem;
}

/* .shop-name{
  font-size: 1.5rem;
} */

.get-point{
  width: 90%;
  margin: 0 auto;
}

.point-use{
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  margin: 0 auto 4rem;
  
}

.line-point{
  width: 13rem;
  height: 5.5rem;
}

.point-text{
  width: 11.5rem;
  height: 5rem;
}

.point-shop{
  width: 11.5rem;
  height: 5rem;
}

.white-btn {
  font-size: 1.2rem;
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gray-btn {
  font-size: 1.2rem;
  color: #FFFFFF;
  background-color: #2A2B23;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer{
  background-image: url('../imges/footer.jpg');
  background-size: cover;
  width: 100%;
  position: relative;
  bottom: 0;
  height: 4rem;
}

